export default {
    name: "app-goods-timer",
    props: {
        startDateTime: String,
        endDateTime: String,
        listStyle: {
            type: Number,
            default() {
                return -1;
            }
        },
        sign: String,
        pageHide: Boolean,
        theme: Object,
    },
    data() {
        return {
            timeInterval: null,
            timer: null,
            timerStr: null
        };
    },
    computed: {
        time() {
            return {
                startDateTime: this.startDateTime,
                endDateTime: this.endDateTime,
                pageHide: this.pageHide,
            };
        },

        listClass() {
            let str = ``;
            if(this.listStyle == 2) {
                str = `main-center`
            }
            return str;
        },

        signName() {
            let name = '';
            switch (this.sign) {
                case 'miaosha':
                    name = '秒杀';
                    break;
                case 'bargain':
                    name = '砍价';
                    break;
                case 'lottery':
                    name = '抽奖';
                    break;
                case 'advance':
                    name = '预售';
                    break;
                case 'flash-sale':
                    name = '限时抢购';
                    break;
            }
            return name;
        },
    },
    methods: {
        timing(startDateTime, endDateTime) {
            let timerStr = null;
            let startTime = this.$utils.timeDifference(new Date().getTime(), new Date(startDateTime).getTime());
            if (startTime) {
                this.timer = '距开始 ';
                timerStr = (startTime['d'] > 0 ? startTime['d'] + '天' : '') + startTime['h'] + ':' + startTime['m'] + ':' + startTime['s'];
            }
            let endTime = null;
            if (!timerStr) {
                endTime = this.$utils.timeDifference(new Date().getTime(), new Date(endDateTime).getTime());
                if (endTime) {
                    this.timer = '距结束 ';
                    timerStr = (endTime['d'] > 0 ? endTime['d'] + '天' : '') + endTime['h'] + ':' + endTime['m'] + ':' + endTime['s'];
                }
            }
            if (!timerStr) {
                this.timer = '活动已结束';
                clearInterval(this.timeInterval);
            }
            this.timerStr = timerStr;
        }
    },
    watch: {
        time: {
            handler() {
                if (this.pageHide) {
                    clearInterval(this.timeInterval);
                    return ;
                }
                let startDateTime = this.startDateTime;
                let endDateTime = this.endDateTime;
                
                startDateTime = startDateTime.replace(/-/g, '/');
                endDateTime = endDateTime.replace(/-/g, '/');
                this.timing(startDateTime, endDateTime);
                this.timeInterval = setInterval(() => {
                    this.timing(startDateTime, endDateTime);
                }, 1000);
            },
            immediate: true
        }
    },
    beforeDestroy() {
        clearInterval(this.timeInterval);
    }
}
